.image-container {
  min-height: 500px;
}

.position-relative {
  position: relative;
  overflow: hidden; 
}

.image {
  width: 100%;
  height: auto;
  max-height: 100%; 
  object-fit: contain; 
}

@media (max-width: 767px) {
  .image {
      position: static;
      width: 100%;
      height: auto; 
  }
  .image-container {
    margin-top: -80px;
    margin-bottom: -120px;
  }
}
